.App {
    min-width: 800px;
    background: #cff8f8;
}

.post {
    display: flex;
    padding: 15px;
    border: 3px solid teal;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}

.post__btns{
    display: flex;
}

.post-enter {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
}

.post-enter-active {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: all 500ms ease-in;
}

.post-exit {
    opacity: 1;
}

.post-exit-active {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
    transition: all 500ms ease-in;
}

.page__wrapper {
    margin: 20px 10px;
    display: flex;
    justify-content: center;
    text-align:  center;
}

.page {
    padding: 10px;
    width: 40px;
    height: 40px;
    border: 1px solid teal;
    cursor: pointer;
    background: rgba(0, 152, 152, 0.3);
}

.page__current {
    border: 3px orange solid;
    font-weight: bold;
    text-align: center;
    font-size: 20px;
    background: rgba(255, 165, 0, 0.5);
}

.postFormBtn{
    margin-top: 30px;
}


