.Loader_loader__3eSiN{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: teal 3px dotted;
    -webkit-animation: Loader_rotate__32sC- 1s infinite linear;
            animation: Loader_rotate__32sC- 1s infinite linear;
}

@-webkit-keyframes Loader_rotate__32sC- {
    from{
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg)  scale(1.4);
                transform: rotate(360deg)  scale(1.4);
    }
}

@keyframes Loader_rotate__32sC- {
    from{
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg)  scale(1.4);
                transform: rotate(360deg)  scale(1.4);
    }
}
